@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  @media (max-width: 767px) {
    padding: 20px 0;
  }

  .container {
    display: flex;
    align-items: center;
    gap: 25px 15px;
    flex-wrap: wrap;
    justify-content: space-between;

    @media (max-width: 1100px) {
      & > *:first-child {
        flex: 1 0 100%;
      }

      & > *:nth-child(2).header__cart {
        margin-left: auto;
      }
    }

    @media (max-width: 670px) {
      & > *:nth-child(2):not(.header__cart) {
        order: 3;
        width: 100%;
      }

      & > *:last-child {
        margin-left: auto;
      }
    }
  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }

  &__cart {
    padding: 0px 0px 0px 5px;
  }
}